@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box}h1,h2,h3,h4,h5,h6{font-size:17px;font-family:'Permanent Marker', cursive;margin:0;margin-bottom:10px}body{font-family:'Kalam', cursive;margin:0}.container{max-width:1200px;margin:0 auto;display:-webkit-flex;display:flex;-webkit-align-items:flex-start;align-items:flex-start}.container .col-left,.container .col-right{-webkit-flex:1 1;flex:1 1;padding:30px}ul{margin:0;padding:0}ul li{list-style:none}.list-item-wrap{margin-bottom:15px}.list-item-wrap .list-item{display:-webkit-flex;display:flex}.list-item-wrap .list-item .list-card{padding:5px 18px;border-radius:10px;background:#fdf6ec;-webkit-flex:1 1;flex:1 1;border:1px dashed #d6c6ad}.list-item-wrap .list-item .list-card-bg{width:10px;margin-right:10px;border-radius:30px}.list-item-wrap .list-item .list-card-bg-red{background:#FF9AA2}.list-item-wrap .list-item .list-card-bg-orange{background:#FEE0CC}.list-item-wrap .list-item .list-card-bg-green{background:linear-gradient(180deg, #b6eebd 35%, #93e99e 100%)}.list-item-wrap .list-card-details{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-left:23px}.list-item-wrap .list-card-details small{margin-right:10px}.form{padding:20px 0}.form form input[type="text"],.form form textarea{width:100%}.form form input[type="text"]{margin-bottom:10px}.form form .form-group-inline{display:-webkit-flex;display:flex;margin-bottom:20px}.form form .form-group-inline .form-task-priority{margin-right:50px}.form form .form-group-inline .form-task-priority input[type="radio"],.form form .form-group-inline .form-task-status input[type="radio"]{margin-right:10px}.form form .length-indicator{text-align:right;margin:0}input,textarea{border:1px solid #c5c5c5}.register-login-link{display:block;margin-top:20px}.register-login-link span{cursor:pointer;text-decoration:underline;font-style:italic;color:blue}.filters{margin-bottom:10px;display:-webkit-flex;display:flex;-webkit-flex-direction:row-reverse;flex-direction:row-reverse}.filters div{margin-right:10px}button,input[type="submit"]{background:white;border:1px dashed grey;margin-right:5px;width:60px;padding:5px 0;cursor:pointer}button:hover,input[type="submit"]:hover{background:#eee}.task-btn{cursor:pointer;font-size:13px}.task-btn:hover{color:grey}

